
import * as React from 'react';
import Sub from './sub';
import { subContext } from '../context/sub'

export default class SubProvider extends React.Component {
    state = {
        text: 'subProvider'
    };

    render() {
        return (
            <subContext.Provider
                value={{
                    text: this.state.text
                }}
            >
                <Sub />

            </subContext.Provider>
        );
    }
}